import React, { Component } from 'react';
import PropTypes from 'prop-types';
import NumericInput from 'react-numeric-input';
import copy from 'copy-to-clipboard';

import QrcodeImg from 'components/QrcodeImg.js';
import AddressName from 'components/AddressName.js';


class ReceiveToken extends Component {
  state = {
    baseUrl: "https://dapp.selftoken.co/#/",
    isAmountChecked: true,
    amount: 100, // SELF
    isUrlCopiedMsgShown: false,
    isAddressCopiedMsgShown: false,
  }

  componentDidMount = () => {
    const baseUrl = window.location.origin + "/#/";
    this.setState({
      baseUrl
    });
  }

  handleInputChange = (event) => {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }

  handleAmountChange = (value) => {
    this.setState({
      amount: value,
    });
  }

  handleCopyUrl = () => {
    copy(this.computeUrl());

    // show "copied" message
    this.setState({
      isUrlCopiedMsgShown: true,
    });

    // hide "copied" message after a few seconds
    setTimeout(() => {
      this.setState({
        isUrlCopiedMsgShown: false,
      });
    }, 2000);
  }

  handleCopyAddress = () => {
    const { accounts } = this.props;
    if (accounts.length === 0) {
      return;
    }

    copy(accounts[0]);

    this.setState({
      isAddressCopiedMsgShown: true,
    });
    setTimeout(() => {
      this.setState({
        isAddressCopiedMsgShown: false,
      });
    }, 2000);
  }

  computeUrl = () => {
    const { accounts } = this.props;

    if (accounts.length === 0) {
      return "";
    }

    const { baseUrl, isAmountChecked, amount } = this.state;

    return baseUrl + "transfer?"
    + "to=" + accounts[0]
    + ( (isAmountChecked && !!amount) ? ("&self=" + amount.toString()) : "" );
  }

  render() {
    const { strings, accounts } = this.props;

    if (accounts.length === 0) {
      return <div>
        <div className="alert alert-danger">{ strings.msg_no_account }</div>
      </div>;
    }

    const { isAmountChecked, amount } = this.state;
    const selfDappUrl = this.computeUrl();

    return (
      <div className="text-center">
        <div className="mb-4">
          <label>
            { strings.My_Wallet_Address }
          </label>
          <input
            type="text"
            className="address-input address-text text-muted"
            value={ accounts[0] }
            onChange={ () => {} }
          />
          <AddressName address={ accounts[0] } />
        </div>

        <div className="mb-5">
          <input
            type="checkbox"
            name="isAmountChecked"
            checked={ isAmountChecked }
            onChange={ this.handleInputChange }
            className="form-check-input"
          />
          <label htmlFor="amountCheckbox">
            { strings.Receive_Amount } ({ strings.EN_SELF })
          </label>
          { isAmountChecked &&
            <NumericInput
              id="receiveAmountInput"
              type="number"
              pattern="[\d\.]*"
              mobile
              className="numeric-input"
              min={0}
              step={10}
              value={ amount }
              onChange={this.handleAmountChange}
            />
          }
        </div>

        <div className="mb-4">
          <h4>{ strings.The_Payer_Pay_with_SELF_DAPP }</h4>
          <div className="mb-1">
            <div>{ strings.msg_please_scan_with_any_qr_code_scanner_1 }</div>
            <div>{ strings.msg_please_scan_with_any_qr_code_scanner_2 }</div>
          </div>
          <div className="mb-1">
            <QrcodeImg text={ selfDappUrl } width={ 320 } margin={ 2 } />
          </div>
          <div className="mb-1 font-condensed word-wrap-break-word text-white">{ selfDappUrl }</div>
          <button className="solid-rectangle-button bg-golden" onClick={this.handleCopyUrl}>
            <span>{ strings.Copy_URL }</span>
          </button>
          { this.state.isUrlCopiedMsgShown &&
            <span className="copied-msg">{ strings.msg_copied }</span>
          }
        </div>

        <div className="mb-4">
          <h4>{ strings.The_Payer_Pay_with_Any_Wallet }</h4>
          <div className="mb-1">
            <div>
              <span>{ strings.msg_please_send_self_1 }</span>
              <span className="text-white"> { isAmountChecked ? amount : '' } SELF </span>
              <span>{ strings.msg_please_send_self_2 }</span>
            </div>
          </div>
          <div className="mb-1">
            <QrcodeImg text={ accounts[0] } width={ 320 } margin={ 2 } />
          </div>
          <div className="mb-1 font-condensed word-wrap-break-word text-white">{ accounts[0] }</div>
          <button className="solid-rectangle-button bg-golden" onClick={this.handleCopyAddress}>
            <span>{ strings.Copy_Address }</span>
          </button>
          { this.state.isAddressCopiedMsgShown &&
            <span className="copied-msg">{ strings.msg_copied }</span>
          }
        </div>
      </div>
    );
  }
}

ReceiveToken.propTypes = {
  accounts: PropTypes.array,
  strings: PropTypes.object,
};

ReceiveToken.defaultProps = {
  accounts: [],
  strings: {},
};

export default ReceiveToken;
