import React, { Component } from "react";
import PropTypes from 'prop-types';
import NumericInput from 'react-numeric-input';
import ethUtil from 'ethereumjs-util';
import qs from 'qs';
import Switch from "react-switch";

import BigNumber from 'utils/BigNumber';
import sig2vrs from "utils/sig2vrs";
import api from "utils/api";
import hexToEvenLengthHex from "utils/hexToEvenLengthHex";
import checkIsMerchant from "utils/checkIsMerchant";

import PleaseOpenInDappBrowser from "components/PleaseOpenInDappBrowser.js";
import { SpinnerCover } from "components/Spinner.js";
import AddressName from 'components/AddressName.js';

// import styles from "../styles/DelegatedTransferForm.module.css";


const hexStrToBuffer = (hexStr) => {
  return Buffer.from(ethUtil.stripHexPrefix(hexStr), 'hex');
};

const bufferToHexStr = (buffer) => {
  return '0x' + buffer.toString('hex');
};

const isSameAddress = (address1, address2) => {
  return address1.toLowerCase() === address2.toLowerCase();
};

// const valid_speeds = new Set(['fast', 'average', 'safeLow']);
const speed_mappings = {
  'fast':     'fast',
  'f':        'fast',
  'average':  'average',
  'a':        'average',
  'normal':   'average',
  'n':        'average',
  'safeLow':  'safeLow',
  'slow':     'safeLow',
  's':        'safeLow',
};
const mapSpeed = (str) => speed_mappings[str];
const defaultIsSpeedFast = false;
const isSpeedFastToSpeed = (isSpeedFast) => {
  return (isSpeedFast) ? 'fast' : 'safeLow';
}
const defaultSpeed = isSpeedFastToSpeed(defaultIsSpeedFast);
const defaultC2BSpeed = 'fast';

const speedConfigs = {
  'fast': {
    'fee': 2, // 1 SELF
    'watingTime': 1, // minutes
  },
  'average': {
    'fee': 1,
    'watingTime': 3,
  },
  'safeLow': {
    'fee': 0.5,
    'watingTime': 5,
  },
};
const getSpeedConfig = (speed) => {
  return (speedConfigs.hasOwnProperty(speed)) ? speedConfigs[speed] : speedConfigs[defaultSpeed];
};

const api_base_url = "https://api.selftoken.co";
const local_api_base_url = "http://localhost:8080";
// const local_api_base_url = "https://testapi.selftoken.co";

const server_wallet_api_path = "/v1/getServerWalletAddress";
const delegatedTransfer_api_path = "/v1/delegatedTransfer";

class DelegatedTransferForm extends Component {
  state = {
    delegate: "0x548574d699FB59bdA110Cc25d9Eaf59D3Ae80E0e", // "0x0000000000000000000000000000000000000000" if no specific delegate
    recipient: "",
    amount: 100, // SELF
    orderId: null,
    isUsingLocalServer: false, // only works on a client hosted by localhost
    server_wallet_address_api_url: api_base_url + server_wallet_api_path,
    delegatedTransfer_api_url: api_base_url + delegatedTransfer_api_path,
    isShowingSpinner: false,
    isSpeedFast: defaultIsSpeedFast,
    speed: isSpeedFastToSpeed(defaultIsSpeedFast),
    prevQueries: {},
  };

  componentDidMount = async () => {
    const { web3 } = this.props;

    if (!web3) {
      return;
    }

    console.log("DelegatedTransferForm this.props", this.props);

    this.parseQuery(this.props);

    // now only work on localhost
    if (window.location.hostname === "localhost" || window.location.hostname === "127.0.0.1") {
      if (this.state.isUsingLocalServer) {
        await this.setState({
          server_wallet_address_api_url: local_api_base_url + server_wallet_api_path,
          delegatedTransfer_api_url: local_api_base_url + delegatedTransfer_api_path,
        });
      }
    }
    console.log("this.state.delegatedTransfer_api_url", this.state.delegatedTransfer_api_url);

    // get delegate address (= server wallet address)
    api(this.state.server_wallet_address_api_url, {}, (data) => {
      console.log(this.state.server_wallet_address_api_url, "data", data); // address string
      this.setState({
        delegate: data,
      });
    }, (error) => {
      console.error(this.state.server_wallet_address_api_url, "error", error);
    });
  }

  // // called when props or state changed
  // componentWillUpdate = (prevProps) => {}

  // called when props changed, not called when componentDidMount
  componentWillReceiveProps = (nextProps) => {
    this.parseQuery(nextProps);
  }

  parseQuery = (props) => {
    if (!props.web3 || !props.location || !props.location.search) {
      return;
    }

    const { web3 } = props;
    const queryString = props.location.search;
    const { prevQueries } = this.state;

    const queries = qs.parse(queryString, { ignoreQueryPrefix: true });
    console.log("queries", queries);
    let newState = {};

    if (!!queries.to && queries.to !== prevQueries.to) {
      if (queries.to !== prevQueries.to && web3.utils.isAddress(queries.to)) {
        console.log("DelegatedTransferForm: query_to correctly parsed");
        newState.recipient = queries.to;
      } else {
        console.warn("DelegatedTransferForm: query_to is provided but not an address");
      }
    }

    if (!!queries.self && queries.self !== prevQueries.self) {
      const amount = parseFloat(queries.self);
      if (!isNaN(amount)) {
        console.log("DelegatedTransferForm: self amount parsed, get", amount);
        newState.amount = amount;
      } else {
        console.warn("DelegatedTransferForm: queries.self is provided but parseFloat returns NaN.");
      }
    }

    if (!!queries.speed && queries.speed !== prevQueries.speed) {
      const speed = mapSpeed(queries.speed);
      if (!!speed && (speed !== prevQueries.speed)) {
        newState.speed = speed;
        console.log("DelegatedTransferForm: speed updated to", speed);
      } else {
        console.warn("DelegatedTransferForm: query_speed is provided but invalid.");
      }
    }

    newState.prevQueries = queries;

    if (!!newState) {
      this.setState(newState);
    }
  }

  handleAmountChange = (value) => {
    this.setState({
      amount: value,
    });
  }

  handleRecipientChange = (event) => {
    this.setState({
      recipient: event.target.value.trim(),
    });
  }

  handleOrderIdChange = (value) => {
    this.setState({
      orderId: value,
    });
  }

  handleSpeedSwitchChange = (value) => {
    const isSpeedFast = !this.state.isSpeedFast;
    const speed = isSpeedFastToSpeed(isSpeedFast)

    this.setState({
      isSpeedFast,
      speed,
    });
  }

  handleDelegatedTransferFormSubmit = async (event) => {
    event.preventDefault();

    console.log("==== handleDelegatedTransferFormSubmit ====");

    const {
      web3,
      accounts,
      delegatedTransferOperator,
      networkId,
      strings,
      selfBalanceBN,
      callback
    } = this.props;

    const {
      recipient,
      amount, // number
      delegate,
      delegatedTransfer_api_url,
      orderId,
      speed,
    } = this.state;

    if (accounts.length === 0) {
      alert(strings.msg_no_account + "\n" + strings.msg_please_unlock);
      return;
    }

    const amountWeiBN = new BigNumber(amount).times(1e18);
    let feeWeiBN = new BigNumber(getSpeedConfig(speed).fee).times(1e18);
    let used_speed = speed;
    if (checkIsMerchant(recipient)) {
      feeWeiBN = new BigNumber(0);
      used_speed = defaultC2BSpeed;
    }

    console.log("recipient", recipient);
    console.log("amount", amount, typeof amount);
    console.log("amountWeiBN", amountWeiBN.toString());

    // check address
    if (!web3.utils.isAddress(recipient)) {
      alert(strings.msg_invalid_recipient_address);
      return;
    }
    if (recipient === "0x0000000000000000000000000000000000000000") {
      alert(strings.msg_recipient_can_not_be_zero);
      return;
    }

    // check balance enough
    if (amountWeiBN.plus(feeWeiBN).isGreaterThan(selfBalanceBN)) {
      alert(strings.msg_not_enough_self);
      return;
    }
    if (amountWeiBN.eq(0)) {
      alert(strings.msg_self_amount_can_not_be_zero);
      return;
    }

    const _operator = delegatedTransferOperator._address;
    const _to = recipient;
    const _delegate = delegate;
    const _value = amountWeiBN.toString();
    const _fee = feeWeiBN.toString();
    const _nonce = (Math.floor(Date.now() / 1000)).toString();
    const _userDataNumber = orderId;
    const _userData = (
      (typeof _userDataNumber === "number") ?
      hexToEvenLengthHex(web3.utils.numberToHex(_userDataNumber)) :
      "0x"
    );
    console.log("_operator", _operator);
    console.log("_to", _to);
    console.log("_delegate", _delegate);
    console.log("_value", _value);
    console.log("_fee", _fee);
    console.log("_nonce", _nonce);
    console.log("_userData", _userData);

    // delegationHash is a 32-byte ("0x" + 64 chars) hex string.
    const delegationHash = web3.utils.soliditySha3(
      {t: "address", v: _operator},
      {t: "address", v: _to},
      {t: "address", v: _delegate},
      {t: "uint256", v: _value},
      {t: "uint256", v: _fee},
      {t: "uint256", v: _nonce},
      {t: "bytes", v: _userData},
    );
    console.log("delegationHash", delegationHash);

    // delegationSignature is a 65-byte ("0x" + 130 chars) hex string.
    const _signature = await web3.eth.sign(delegationHash, accounts[0]);
    const _vrs = sig2vrs(_signature);
    const _int_v = parseInt(_vrs.v);
    console.log("_signature", _signature);
    console.log("_vrs", _vrs);

    if (_int_v < 27) {
      _vrs.v = web3.utils.toHex(_int_v + 27);
    }
    console.log("_vrs.v", _vrs.v);

    if (_int_v !== 27 && _int_v !== 28) {
      throw new Error("_vrs.v not 27 or 28");
    }

    const prefixedDelegationHash = web3.utils.soliditySha3(
      { t: "string", v: "\x19Ethereum Signed Message:\n32" },
      { t: "bytes32", v: delegationHash },
    );

    const recoveredAddressFromNotPrefixedHash = bufferToHexStr(
      ethUtil.pubToAddress(
        ethUtil.ecrecover(
          hexStrToBuffer(delegationHash),
          parseInt(_vrs.v),
          _vrs.r,
          _vrs.s,
        )
      )
    );
    const recoveredAddressFromPrefixedHash = bufferToHexStr(
      ethUtil.pubToAddress(
        ethUtil.ecrecover(
          hexStrToBuffer(prefixedDelegationHash),
          parseInt(_vrs.v),
          _vrs.r,
          _vrs.s,
        )
      )
    );
    console.log("recoveredAddressFromNotPrefixedHash", recoveredAddressFromNotPrefixedHash);
    console.log("recoveredAddressFromPrefixedHash", recoveredAddressFromPrefixedHash);

    const isRecoveredWithoutPrefix = isSameAddress(recoveredAddressFromNotPrefixedHash, accounts[0]);
    const isRecoveredWithPrefix = isSameAddress(recoveredAddressFromPrefixedHash, accounts[0]);

    const isCorrectlyRecovered = isRecoveredWithoutPrefix || isRecoveredWithPrefix;
    console.log("isCorrectlyRecovered", isCorrectlyRecovered);

    if (!isCorrectlyRecovered) {
      throw new Error("address is not correctly recovered from the signature");
    }

    const _signedWithPrefix = isRecoveredWithPrefix;
    console.log("_signedWithPrefix", _signedWithPrefix);


    const tx_data = delegatedTransferOperator.methods.transferPreSigned(
      _to,
      _delegate,
      _value,
      _fee,
      _nonce,
      _userData,
      _signedWithPrefix,
      // _signature
      _vrs.r,
      _vrs.s,
      _vrs.v
    // );
    ).encodeABI();

    const params = {
      _to,
      _delegate,
      _value,
      _fee,
      _nonce,
      _userData,
      _signedWithPrefix,
      _sig_r: _vrs.r,
      _sig_s: _vrs.s,
      _sig_v: _vrs.v,
    };
    const transfer_from = accounts[0];

    const post_data = {
      networkId,
      tx_data,
      params,
      transfer_from,
      speed: used_speed,
    }
    console.log("post_data", post_data);

    this.setState({
      isShowingSpinner: true,
    });
    api(delegatedTransfer_api_url, post_data, (order) => {
      // if transaction is broadcasted
      // `data` is `order`
      console.log("order", order);
      this.setState({
        isShowingSpinner: false,
      });
      alert(
        strings.msg_transaction_submitted + "\n" +
        strings.Transaction_Number + ": " + order._id
      );
      callback();
    }, (error) => {
      // if failed
      console.error("error", error);
      this.setState({
        isShowingSpinner: false,
      });
      alert(
        strings.msg_tx_failed + strings.msg_possible_cause + "\n" +
        "1. " + strings.msg_not_enough_self + "\n" +
        "2. " + strings.msg_error_in_server + "\n" +
        strings.msg_please_contact_us_and_let_us_help_you
      );
    });
  }

  render() {
    const { web3, strings, selfBalanceBN } = this.props;
    const { amount, recipient, isShowingSpinner, isSpeedFast, speed } = this.state;

    if (!web3) {
      return <div>
        <div className="narrow-container mb-5">
          <div className="alert alert-danger">
            <PleaseOpenInDappBrowser
              strings={ strings }
            />
          </div>
        </div>
      </div>;
    }

    const amountWeiBN = (new BigNumber(amount)).times(1e18);
    const feeWeiBN = new BigNumber(getSpeedConfig(speed).fee).times(1e18);
    const isTokenNotEnough = amountWeiBN.plus(feeWeiBN).isGreaterThan(selfBalanceBN);

    const isAddressInvalid = !web3.utils.isAddress(recipient);

    return (<div>
      {
        isShowingSpinner &&
        <SpinnerCover/>
      }
      <form
        onSubmit={this.handleDelegatedTransferFormSubmit}
        id="transfer-form"
        className="transfer-form text-center"
      >
        <div className="mb-4">
          <label htmlFor="delegatedTransferRecipientInput">{ strings.Recipient_Wallet_Address }</label>
          <input
            type="text"
            className={
              "address-input address-text" +
              ( isAddressInvalid ? " text-danger" : "" )
            }
            id="delegatedTransferRecipientInput"
            value={ recipient }
            onChange={ this.handleRecipientChange }
            placeholder="0x0000000000000000000000000000000000000000"
          />
          <AddressName address={ recipient } />
        </div>

        <div className="mb-4">
          <label htmlFor="transferAmount">{ strings.Transfer_Amount } ({ strings.EN_SELF })</label>
          <NumericInput
            id="transferAmount"
            type="number"
            pattern="[\d\.]*"
            mobile
            className={
              "numeric-input" + ( isTokenNotEnough ? " text-danger" : "" )
            }
            min={0}
            step={10}
            value={this.state.amount}
            onChange={this.handleAmountChange}
          />
        </div>

        {/* <div className="mb-4">
          <label htmlFor="orderId">Order ID</label>
          <NumericInput
            id="orderId"
            mobile
            className="numeric-input"
            min={0}
            step={1}
            value={this.state.orderId}
            onChange={this.handleOrderIdChange}
          />
        </div> */}

        {
          !checkIsMerchant(recipient) &&
          <div>
            <div className="mb-4">
              <label>{ strings.Speed }</label>
              <div>
                <span
                  className={ "align-middle mr-2 text-right d-inline-block " +
                    (isSpeedFast ? 'text-muted' : 'text-white')
                  }
                  style={{ width: "4rem" }}
                >
                  { strings.Normal }
                </span>
                <Switch
                  id="speed-switch"
                  onChange={this.handleSpeedSwitchChange}
                  onClick={this.handleSpeedSwitchChange}
                  checked={ isSpeedFast }
                  onColor="#d6a838"
                  uncheckedIcon={ false }
                  checkedIcon={ false }
                  height={ 24 }
                  width={ 48 }
                  className="align-middle"
                />
                <span
                  className={ "align-middle ml-2 text-left d-inline-block " +
                    (isSpeedFast ? 'text-white' : 'text-muted')
                  }
                  style={{ width: "4rem" }}
                >
                  { strings.Fast }
                </span>
              </div>
            </div>

            <div className="mb-4">
              <div className="transfer-info-container">
                <div className="clearfix font-weight-bold mb-1">
                  <span className="float-left">{ strings.Average_Wating_Time }</span>
                  <span className="float-right text-white">{ getSpeedConfig(speed).watingTime } min</span>
                </div>
                <div className="clearfix font-weight-bold">
                  <span className="float-left ">{ strings.Transaction_Fee }</span>
                  <span className="float-right text-white">{ getSpeedConfig(speed).fee } SELF</span>
                </div>
              </div>
            </div>
          </div>
        }

        <div>
          <button type="submit" className="solid-rectangle-button bg-golden">
            <span>{ strings.Transfer }</span>
          </button>
        </div>
      </form>
    </div>);
  }
}

DelegatedTransferForm.propTypes = {
  web3: PropTypes.object,
  accounts: PropTypes.array,
  delegatedTransferOperator: PropTypes.object,
  networkId: PropTypes.number,
  strings: PropTypes.object,
  selfBalanceBN: PropTypes.object,
  location: PropTypes.object,
  callback: PropTypes.func,
};

DelegatedTransferForm.defaultProps = {
  web3: {},
  accounts: [],
  delegatedTransferOperator: {},
  networkId: 1,
  strings: {},
  selfBalanceBN: new BigNumber(),
  location: {},
  callback: () => {},
};

export default DelegatedTransferForm;
